@import url('https://fonts.googleapis.com/css?family=Lora:700|Roboto&subset=latin-ext');

.App {
  text-align: center;
}

.App-header {
  background-color: rgba(20,20,20,0.8);
  padding: 20px;
  color: white;
}

.App-header h1{
  font-size: 1.5em;
  font-family: 'Lora', serif;
}

.App-title {
  margin: 0;
}

.App-intro {
  font-size: large;
}

@media screen and (max-width: 1000px) {
  .App-header h1{
    font-size: 1.2em;
  }
}


